<template>
	<div>
		<h3>批量应用地域模板</h3> 
        <div v-if="tplList.length<=1">       
      尚未创建模板， <router-link to="/bat/areaCreate">点此创建。</router-link>      
      </div>
      
    <div v-for="item in tplList" style="margin-bottom: 10px;"> 
    <el-card class="box-card"  shadow="hover">

      <div slot="header" class="clearfix">
        <span>模板：{{item.tpl_name}}</span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="deleteTpl(item)"><i class="el-icon-delete"></i>
</el-button>
      </div>
      <div  class="text item" >
        {{item.source_data}}
      </div>
      
      <div  class="text item" >
        <el-button @click="clickApply(item)">应用</el-button> 
         <!-- <el-button @click="editItem(item)">查看</el-button> -->
        
      </div>
    </el-card>
    </div>
    
    
<el-drawer
  title="选择应用到的计划"
  :visible.sync="drawer"
  :direction="direction"
  size="50%">
  <div  style="margin:20px;">
    <el-checkbox-group v-model="checkedList"  style="margin: 20px; max-height:400px; width:80%;overflow-y: auto">
      <el-row  v-for="item in CampaignSchedule">
        <el-col ><el-checkbox :label="item.campaign_id" >{{item.title}}</el-checkbox></el-col>
      </el-row>
    </el-checkbox-group>
    
  <el-button @click="Apply" type="primary" :loading="false" style="margin-top: 10px;">立即应用</el-button>


  </div>
</el-drawer>
    
	</div>
</template>

<script>
  
  import {request} from "@/network/request.js";
  import Qs from 'qs';
    
    
  export default {
    data() {
      return {
        
        tplList:[],
        CampaignSchedule:[],
        drawer: false,
        direction: 'rtl',
        checkedList:[],
        schedule:'',
      }
    },

    methods:{
        getAreaTpl(){
          
          request({
            url:"/subway/getareaTpl",
            method: 'get',
          }).then( res => {            
            console.log(res)            
            this.tplList=res.data;
          })
        },
        getCampaignSchedule() {
          
            request({
              url:"/subway/getCampaignSchedule",
              method: 'get',            
            }).then( res => {            
              console.log(res.data)            

              console.log(typeof res.data )
              if(typeof res.data == "object")
              {
                this.CampaignSchedule=res.data;
              }

            })
          
          
        },
        deleteTpl(item) {
          
          this.$confirm('是否继续删除?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消'
                  }).then(() => {
                    
                    request({
                      url:"/subway/delAreaTpl",
                      method: 'POST',
                      data:Qs.stringify({
                        id:item.id
                      }),
                    }).then( res => {            
                      console.log(res)
                      
                      //去掉此条记录
                      this.getAreaTpl();
                    
                    })
                    this.$message({
                      type: 'success',
                      message: '删除成功!'
                    });
                  })

        },
        
        //打开计划选择框
        clickApply(item){
          this.schedule = item.tpl_content;
          this.drawer = true;
        },
        //应用到计划
        Apply()
        {
          
           console.log(this.CampaignSchedule);
          
          for(var i=0;i<this.checkedList.length;i++)
          {
        
            request({
              url:"/subway/updateCampaignArea",
              method: 'POST',
              data:Qs.stringify({
                campaign_id: this.checkedList[i],
                schedule:this.schedule,
              }),
            }).then( res => {
              console.log(res)
              if(typeof  res.data.data.campaign_area.campaign_id != 'undefined')
              {
                let cid = res.data.data.campaign_area.campaign_id;
                this.$message({
                  type: 'success',
                  message: cid+'设置成功!'
                });
              } else {
                
                this.$message({
                  type: 'fail',
                  message: '设置失败!'
                });
              }
              
              
            })
          }
        },
        editItem(item)
        {
            this.$router.push({ path:'/bat/areaCreate', query:{id:item.id}})
        }
        
    },
    mounted() {
      this.getAreaTpl();
      
      this.getCampaignSchedule();
      
    },
    watch: {
        // 如果路由有变化，会再次执行该方法
        '$route': 'getAreaTpl'
      },
  }

</script>

<style>
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
</style>
